<template><section>

<h1>Icon 图标</h1>
<p>语义化的矢量图形，使用开源的 Iconfont 作为图标库，并制作成了 icon font。</p>
<h3>使用方法</h3>
<p>使用 class=&quot;o-icon...&quot; 来声明图标，具体图标的名称请 copy 相应的标签</p>
<div class="demo-block icon-show">
    <i class="o-icon_roundclose_fill"></i>
    <i class="o-icon_setting_fill"></i>
    <i class="o-icon-homepage"></i>
    <i class="o-icon-brush_fill"></i>
</div>
<demo-block>
                        <div slot="desc"><i class="o-icon_roundclose_fill"></i>
<i class="o-icon_setting_fill"></i>
<i class="o-icon-homepage"></i>
<i class="o-icon-brush_fill"></i>
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"o-icon_roundclose_fill"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"o-icon_setting_fill"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"o-icon-homepage"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"o-icon-brush_fill"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>图标示例</h3>
<ul class="icon-list icon-show">
  <li v-for="name in icons" :key="name">
    <span>
      <i :class="'o-' + name"></i>
      {{'o-' + name}}
    </span>
  </li>
</ul>
</section></template>
<style lang="less" type="text/less" scoped>
ul.icon-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 0 !important;
    border: 1px solid #eaeefb;
    border-radius: 4px;    
}
.icon-list li {
    display: block;
    width: 20%;
    height: 120px;
    line-height:120px;
    text-align: center;
    transition: color .15s linear;
    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-right: -1px;
    margin-bottom: -1px;
    
}
.icon-list li span{
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
    font-family: Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,SimSun,sans-serif;
    color: #99a9bf;
}
.icon-list li i {
    display: block;
    font-size: 24px;
    margin-bottom: 15px;
    color: #3f536e;
}
</style>
<script>
  var iconList = require('../icon.json');
  export default {
    data() {
      return {
        icons: iconList
      };
    }
  }
</script>


